<template>
  <BlockSection height="310px" title="金额情况">
    <div class="section__body row">
      <div class="left flex-main">
        <div class="item row income">
          <div class="icon">
            <img src="/pages/dashboard/reports/g7_1.png" />
          </div>
          <div class="content flex-main">
            <div class="title">累计支出金额</div>
            <div class="value">
              <span>{{ data.expend }}</span>
              <span class="unit">/ 元</span>
            </div>
          </div>
        </div>
        <div class="item row expend">
          <div class="icon">
            <img src="/pages/dashboard/reports/g7_2.png" />
          </div>
          <div class="content flex-main">
            <div class="title">累计支出金额</div>
            <div class="value">
              <span>{{ data.expend }}</span>
              <span class="unit">/ 元</span>
            </div>
          </div>
        </div>
      </div>
      <div class="right">
        <div class="chart flex-main">
          <div class="outer-chart">
            <div ref="chart" class="inner-chart"></div>
          </div>
        </div>
      </div>
    </div>
  </BlockSection>
</template>

<script>
import { dashboardBlockMixin } from "@/components/dashboard/mixins/dashboardMixin.js";

export default {
  mixins: [dashboardBlockMixin("bizFinBalance")],

  data() {
    return {};
  },

  computed: {
    chartOptions() {
      return {
        color: ["#1871fe", "#f3961c"],
        title: {
          text: `${this.data.total}元` || "--", //主标题文本
          left: "center",
          top: "42%",
          textStyle: { fontSize: 24, color: "#f2f2f2", align: "center" },
        },
        series: [
          {
            type: "pie",
            radius: ["60%", "80%"],
            avoidLabelOverlap: false,
            emphasis: { label: { show: false } },
            label: {
              alignTo: "edge",
              formatter: "{name|{b}}\n{percent|{d}%}",
              minMargin: 5,
              edgeDistance: 0,
              lineHeight: 20,
              rich: {
                name: {
                  fontSize: 14,
                  fontWeight: "bold",
                  color: "#fff",
                  width: 30,
                },
                percent: {
                  fontSize: 14,
                  fontWeight: "bold",
                  color: "#ffff00",
                  width: 50,
                },

                labelLayout: (params) => {
                  const isLeft = params.labelRect.x < this.chart.getWidth() / 2;
                  const points = params.labelLinePoints;
                  points[2][0] = isLeft
                    ? params.labelRect.x
                    : params.labelRect.x + params.labelRect.width + 10;

                  return {
                    labelLinePoints: points,
                  };
                },
                value: { fontSize: 14, fontWeight: "bold", color: "#33ccff" },
              },
            },
            labelLine: {
              length: 30,
              length2: 70,
            },
            data: [
              { name: "支出", value: this.data.expend },
              { name: "收入", value: this.data.income },
            ],
          },
        ],
      };
    },
  },

  mounted() {},
};
</script>

