<template>
  <BlockSection height="270px">
    <div class="section__header">
      <div class="title">主数据获取总量</div>
      <div class="total">{{ data.total }}</div>
    </div>
    <div class="section__body row" style="padding: 0 10px">
      <div class="chart flex-main">
        <div class="outer-chart">
          <div ref="chart" class="inner-chart"></div>
        </div>
      </div>
    </div>
  </BlockSection>
</template>

<script>
import { dashboardBlockMixin } from "@/components/dashboard/mixins/dashboardMixin.js";

export default {
  mixins: [dashboardBlockMixin("masterDataRequestTotal")],

  data() {
    return {};
  },

  computed: {
    chartOptions() {
      return {
        color: ["#a034d5", "#ae1937", "#c06e0e", "#c7ae17", "#1b8fad"],
        series: [
          {
            type: "pie",
            radius: [20, 60],
            center: ["50%", "50%"],
            itemStyle: { borderWidth: 1, borderColor: "#000000" },
            left: "5%",
            right: "20%",
            top: 0,
            bottom: 0,
            label: {
              alignTo: "edge",
              formatter: "{name|{b}}  {percent|{d}%}\n{value|{c}}",
              minMargin: 5,
              edgeDistance: 0,
              lineHeight: 20,
              rich: {
                name: {
                  fontSize: 14,
                  fontWeight: "bold",
                  color: "#fff",
                  width: 30,
                },
                percent: {
                  fontSize: 14,
                  fontWeight: "bold",
                  color: "#ffff00",
                  width: 50,
                },
                value: { fontSize: 14, fontWeight: "bold", color: "#33ccff" },
              },
            },
            labelLine: {
              length: 10,
              length2: 0,
            },
            labelLayout: (params) => {
              const isLeft = params.labelRect.x < this.chart.getWidth() / 2;
              const points = params.labelLinePoints;
              points[2][0] = isLeft
                ? params.labelRect.x
                : params.labelRect.x + params.labelRect.width + 10;
              return {
                labelLinePoints: points,
              };
            },
            data: this.data.list || [],
          },
        ],
      };
    },
  },
};
</script>

