  &__header {
    height: 82px;
    background: rgba(52, 128, 234, 0.2);
    .title {
      text-align: left;
      padding: 5px 40px 0 40px;
    }

    .total {
      text-align: right;
      padding: 0 100px;
      color: #fedb65;
      font-size: 28px;
      font-weight: bold;
    }
  }
}

.chart {
  padding: 10px;
  .inner-chart {
    width: 432px;
    height: 160px;
  }
}
