<template>
  <div class="page-section section" :style="sectionStyle">
    <div v-if="$slots.header || title" class="section__header">
      <slot name="header">
        <div v-if="title" class="title">{{ title }}</div>
      </slot>
    </div>
    <div class="section__body">
      <slot />
    </div>
  </div>
</template>

<script>
export default {
  props: ["title", "width", "height"],

  data() {
    return {};
  },

  computed: {
    sectionStyle() {
      return {
        width: this.width || "100%",
        height: this.height || "auto",
      };
    },
  },

  mounted() {},
  beforeDestroy() {},
};
</script>

