  &__header {
    padding: 5px;
    .title {
      color: white;
      font-size: 23px;
      font-weight: bold;
      text-align: center;
    }
  }

  &__body {
    box-sizing: border-box;
    border: 2px solid #2c446b;
  }
}
</style>
